@import 'variables';
@import 'mobile-variables';


#total.is-menu-open #allcontent {
    pointer-events: none;
    transition: all $duration ease-out;
    -webkit-filter: blur(8px);
    -moz-filter: blur(8px);
    -ms-filter: blur(8px);
    filter: blur(8px);
    //background-color: #b0b0b0;
}

.active-mobile-header {
    width: 100%;
    height: 100%;

}

.mobile-header {
    width: 100%;
    height: 100%; //$title-height;
    position: fixed;
    z-index: 10;


    .header-top {
        z-index: 10;
        pointer-events: auto;
    }

    .option {
        position: absolute;
        bottom: 0px;

        .sns-block {
            display: flex;

            .twitter {
                cursor: pointer;
                width: $option-icon-size;
                height: $option-icon-size;
                background-size: $option-icon-size $option-icon-size;
                background-repeat: no-repeat;
                background-image: url(../img/twitter.jpg);
            }

            .fb {
                cursor: pointer;
                width: $option-icon-size;
                height: $option-icon-size;
                background-size: $option-icon-size $option-icon-size;
                background-repeat: no-repeat;
                background-image: url(../img/fb.png);
            }
        }


        .lang-block {
            display: flex;

            .active-lang {
                background-color: $active-block-color;
            }

            p {
                width: calc(#{$side-menu-width / 2});
                font-size: 10px;
                text-align: center;
                height: 30px;
                line-height: 30px;
                border-top: solid 1px $background-color;
            }

            p:nth-child(0) {
                border-right: solid 1px $background-color;
            }
        }
    }

    .mobile-header-content {
        height: 100%;
        overflow: hidden;
        position: relative;

        .header-back {
            position: absolute;
            width: 100%;
            height: 100%;
            opacity: 0;
            -webkit-transition: opacity 1s ease-out;
            -moz-transition: opacity 1s ease-out;
            -o-transition: opacity 1s ease-out;
            transition: opacity 1s ease-out;
            display: none;
            pointer-events: auto;
        }

        .active-header-back {
            background-color: $background-color;
            opacity: 0.8;
            display: block;
        }

        .mobile-title {
            background-color: $background-color;
            position: absolute;
            height: $title-height;
            line-height: $title-height;
            width: 100%;
            text-align: center;
            z-index: 10;
            -webkit-transform: translateX(0%);
            transform: translateX(0%);
            transition-duration: $duration;

            .lab {
                font-size: 20px;
                height: $title-height;
                line-height: $title-height;
            }
        }

        .disables {
            -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
            transition-duration: $duration;
        }

        .side-menu {
            pointer-events: auto;
            position: absolute;
            margin-top: $title-height;
            z-index: 20;
            height: calc(100% - #{$title-height});
            background-color: #f0f0f4;
            width: $side-menu-width;
            color: $text-color;
            margin-left: calc(-1 * #{$side-menu-width});
            -webkit-transition: margin-left $duration ease-out;
            -moz-transition: margin-left $duration ease-out;
            -o-transition: margin-left $duration ease-out;
            transition: margin-left $duration ease-out;
        }

        .active-side-menu {
            margin-left: 0px;
        }

        //.menus {
        // margin-top: $title-height;
        // height: calc(100% - #{$title-height});
        // background-color: #f0f0f4;
        // width: 300px;
        //}
    }
}


.mobile-menu-block {
    background-color: $background-color;
    opacity: 0.9;
    height: 100%;
    z-index: 100;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
    transition-duration: $duration;

    .menu-wrapper {
        text-align: center;
        line-height: 75px;
        font-size: 25px;
        color: $gray;
    }

    .lang-block {
        margin-top: 10px;
        padding-top: 10px;
        border-top: $black 1px solid;
        display: flex;
        justify-content: space-around;
        width: 200px;
        font-size: 20px;

        .selected-lang {
            color: $black;
            font-weight: 550;
        }


    }
}

.selected-menu {
    color: $link-color;
}

.un-selected-menu {
    color: $gray;
}


.mobile-menu {
    text-align: center;
    font-size: 15px;
}

@mixin animationValue($name, $duration, $function, $delay, $count, $state) {
    -webkit-animation: $name $duration+s $function $delay+s $count $state;
    animation: $name $duration+s $function $delay+s $count $state;
}

@-webkit-keyframes example {
    100% {
        opacity: 1;
    }
}

@keyframes example {
    100% {
        opacity: 1;
    }
}

@for $cnt from 0 through 10 {
    .active-mobile-menu:nth-child(#{$cnt}) {
        @include animationValue(example, 0.5, ease, $cnt * 0.1 + 0.25, 1, forwards);
    }

    //.active-mobile-menu-#{$cnt} {
    //    @include animationValue(example, 0.5, ease, $cnt * 0.5 , 1, forwards);
    //}
}

@mixin keyframes($name) {
    @-webkit-keyframes #{$name} {
        @content;
    }

    @keyframes #{$name} {
        @content;
    }
}



.trigger-wrapper {
    width: $title-height;
    height: $title-height;
    background-color: $background-color;
    z-index: 20;
    cursor: pointer;
}

$trigger-width: 20px;
$trigger-height: 18px;

.menu-trigger,
.menu-trigger span {
    display: inline-block;
    transition: all .4s;
    box-sizing: border-box;
}

.menu-trigger {
    margin-top: calc((#{$title-height} - #{$trigger-height}) / 2);
    margin-left: calc((#{$title-height} - #{$trigger-width}) / 2);
    position: relative;
    width: $trigger-width;
    height: $trigger-height;
}

.menu-trigger span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: $title-gray;
    border-radius: 2px;
}

.menu-trigger span:nth-of-type(1) {
    top: 0;
}

.menu-trigger span:nth-of-type(2) {
    top: 8px;
}

.menu-trigger span:nth-of-type(3) {
    bottom: 0;
}

.menu-trigger.active span:nth-of-type(1) {
    -webkit-transform: translateY(8px) rotate(-45deg);
    transform: translateY(8px) rotate(-45deg);
}

.menu-trigger.active span:nth-of-type(2) {
    left: 50%;
    opacity: 0;
    -webkit-animation: active-menu-bar02 .8s forwards;
    animation: active-menu-bar02 .8s forwards;
}

@-webkit-keyframes active-menu-bar02 {
    100% {
        height: 0;
    }
}

@keyframes active-menu-bar02 {
    100% {
        height: 0;
    }
}

.menu-trigger.active span:nth-of-type(3) {
    -webkit-transform: translateY(-8px) rotate(45deg);
    transform: translateY(-8px) rotate(45deg);
}

.dummy {
    color: $background-color;
}