$dmenu-color: #d9e5f7;
$menu-width: 100px;
$menu-height: 30px;

.dmenu {
    position: relative;
    width: 100%;
    height: $menu-height;
    max-width: 1000px;
    margin: 0 auto;
}

.dmenu>li {
    float: left;
    width: $menu-width;
    /* グローバルナビ4つの場合 */
    height: $menu-height;
    line-height: $menu-height;
    background: $background-color;
    list-style: none;
}

.dmenu>li span {
    display: block;
    color: $black;
}

.dmenu>li span:hover {
    color: $black;
}

ul.dmenu__second-level {
    visibility: hidden;
    opacity: 0;
    z-index: 1;
}

ul.dmenu__third-level {
    visibility: hidden;
    opacity: 0;
}

ul.dmenu__fourth-level {
    visibility: hidden;
    opacity: 0;
}

.dmenu>li:hover {
    -webkit-transition: all .5s;
    transition: all .5s;
}

.dmenu__second-level li {
    // border: 1px solid #111;
}

.dmenu__second-level {
    border-left: 1px solid #111;
    border-right: 1px solid #111;
    border-bottom: 1px solid #111;
    max-width: $menu-width;
    box-sizing: border-box;
}


.dmenu__second-level li span:hover {
    background: $dmenu-color;
}

.dmenu__second-level:hover+.init-bottom {
    border: 1px solid $base-blue;
}

/* 下矢印 */
.init-bottom:after {
    content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    margin: 0 0 0 15px;
    border-right: 1px solid $black;
    border-bottom: 1px solid $black;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.dmenu-list {
    list-style: none;
    cursor: pointer;

    span {
        text-align: center;
    }
}

.init-bottom {
    text-align: center;
    border: 1px solid $black;
}

.init-bottom:hover {
    border: 1px solid $black;
}

/* floatクリア */
.dmenu:before,
.dmenu:after {
    content: " ";
    display: table;
}

.dmenu:after {
    clear: both;
}

.dmenu {
    *zoom: 1;
}

.dmenu>li.dmenu__single {
    position: relative;
}

li.dmenu__single ul.dmenu__second-level {
    position: absolute;
    top: $menu-height - 10;
    width: 100%;
    background: $background-color;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
}

li.dmenu__single:hover ul.dmenu__second-level {
    top: $menu-height;
    visibility: visible;
    opacity: 1;
}