.wrapper {
    margin: 0 auto;
    width: 800px;
    /* width: 100%; */
}

.main {
    padding-top: 100px;
}


.header {
    width: 100%;
    height: $header-height;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 20;
    background-color: $background-color;
    opacity: 0.9;
    -webkit-transition: height 0.3s ease-in-out;
    -moz-transition: height 0.3s ease-in-out;
    -o-transition: height 0.3s ease-in-out;
    transition: height 0.3s ease-in-out;
}

//.small-header{
// height: calc(#{$header-height} - 30px);
//}

.header-wrapper {
    width: 100%;
    height: 55px;
}

.title-block {
    display: flex;
    justify-content: center;
}

.hidden-block {
    display: none;
    pointer-events: none;
}

.logo-block {
    cursor: pointer;
    width: 63px;
    height: 50px;
    background-size: 63px 50px;
    background-repeat: no-repeat;
    background-image: url(../../img/logo.png);
    margin-right: 10px;
}

.header-content {
    display: flex;
    position: relative;
    width: $content-width;

    @include mq() {
        width: $content-tablet-width;
    }

    -webkit-justify-content: space-between;
    /* Safari */
    justify-content: space-between;

    .header-top-ja {
        color: $black;
        font-size: 25px;
        line-height: $header-height;
        cursor: pointer;

        .ut {
            color: $black;
        }

        .lab {
            color: $black;
            font-size: 22px;
            text-align: center;
        }
    }

    .header-top-en {
        color: $black;
        cursor: pointer;

        .ut {
            color: $black;
            font-size: 9px;

            div {
                text-align: center;
            }
        }

        .lab {
            color: $black;
            font-size: 20px;
            text-align: center;
        }

        p {
            margin-top: 0px;
            margin-bottom: 0px;
        }
    }

    .option {
        display: flex;
        align-items: center;
        -webkit-justify-content: space-between;

        /* Safari */
        justify-content: space-between;
        width: 100px;

        .lang {
            cursor: pointer;
            color: $title-gray;
            width: 50px;
            margin: 0px;

            &:hover {
                color: $light-gray;
            }
        }

        .twitter {
            cursor: pointer;
            width: $option-icon-size;
            height: $option-icon-size;
            background-size: $option-icon-size $option-icon-size;
            background-repeat: no-repeat;
            background-image: url(../../img/twitter.jpg);
        }

        .fb {
            cursor: pointer;
            width: $option-icon-size;
            height: $option-icon-size;
            background-size: $option-icon-size $option-icon-size;
            background-repeat: no-repeat;
            background-image: url(../../img/fb.png);
        }
    }

    .menus {
        position: absolute;
        display: flex;
        width: 350px;
        height: 100%;

        @include mq() {
            width: 400px;
        }

        -webkit-justify-content: space-between;
        /* Safari */
        justify-content: space-between;
        align-items: center;
        //top: 15px;
        right: 140px;
        color: $title-gray;
        font-size: 15px;


        @include mq(pc) {
            .menu {
                display: inline-block;
                cursor: pointer;
                color: $black;
                font: "Avenir LT W01";

                &::after {
                    content: "";
                    display: block;
                    width: 0;
                    transition: width 0.3s;
                    border-bottom: 2px solid $base-blue;
                    margin: 0 auto;
                }

                &:hover {
                    color: $light-gray;
                }

                &:hover::after {
                    width: 100%;
                }
            }

            .selected-menu {
                display: inline-block;

                &::after {
                    content: "";
                    display: block;
                    width: 100%;
                    border-bottom: 2px solid $base-blue;
                    margin: 0 auto;
                }
            }
        }

        @include mq() {
            .menu {
                display: inline-block;
                cursor: pointer;
                display: block;
                width: 100%;
                margin: 0 auto;
            }

            .selected-menu {
                border-bottom: 1px solid $base-blue;
            }
        }
    }
}