@import 'variables';
@import 'mobile-variables';
@import 'content/dmenu';
@import 'content/header';
@import 'content/home';
@import 'content/publication';
@import 'content/works';
@import 'content/contact';
@import 'mobile';

h1,
h2,
div,
ul,
li,
dl,
dt,
dd,
table,
th,
tr,
td,
p {
  padding: 0;
  margin: 0;
  font-weight: normal;
}

h1 {
  font-size: 22px;
  font-weight: bold;
  color: $title-gray;
  border-bottom: solid 1px #cccccc;
}

a {
  text-decoration: none;
  color: $link-color;
  cursor: pointer;

  &:hover {
    color: $link-hover-color;
  }
}

html {
  font-size: 62.5%;
  height: 100%;
  width: 100%;
  padding: 0px;
  margin: 0px;
  scroll-behavior: smooth;
}

body {
  /*background-color: #fafafa;*/
  font-size: 0.9em;
  font-family: 'Source Sans Pro', Helvetica, Arial, 'ãƒ’ãƒ©ã‚®ãƒŽè§’ã‚´ Pro W3', 'Hiragino Kaku Gothic Pro', 'ãƒ¡ã‚¤ãƒªã‚ª', Meiryo, 'ï¼­ï¼³ ï¼°ã‚´ã‚·ãƒƒã‚¯', 'MS PGothic', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;

  position: relative;

  @include mq(pc) {
    min-width: $content-width;
  }

  @include mq() {
    min-width: $content-tablet-width;
  }

  @include mq(sm) {
    width: 100%;
    min-width: 0px;
  }

  min-height: 100%;
  padding: 0px;
  margin: 0px;
  font-size: 1.3rem;
  background-color: $background-color;
}

footer {
  height: 20px;
  position: absolute;
  margin-top: 50px;
  font-size: 10px;

  @include mq(pc) {
    min-width: $content-width;
  }

  @include mq() {
    min-width: $content-tablet-width;
  }

  @include mq(sm) {
    width: 100%;
    min-width: 0px;
  }

  color: $title-gray;
  text-align: center;

  .copy {
    font-size: 14px;
  }

  a {
    color: $title-gray;
    text-decoration: underline;
  }
}

#app,
#app>div {
  height: 100%;
  width: 100%;
  padding: 0px;
  margin: 0px;
  height: auto !important;
  padding-bottom: 50px
}

.wrapper {
  width: $content-width;
  margin: auto;

  @include mq() {
    width: $content-tablet-width;
  }

  @include mq(sm) {
    width: 90%;
    -webkit-justify-content: center;
    /* Safari */
    justify-content: center;
  }

  min-height: 100%;
  height: auto !important;
}


.visible-content {
  opacity: 1.0;
  -webkit-transition: opacity 0.5s ease-out;
  -moz-transition: opacity 0.5s ease-out;
  -o-transition: opacity 0.5s ease-out;
  transition: opacity 0.5s ease-out;
}

.invisible-content {
  opacity: 0.0;
}

#allcontent {
  @include mq(pc) {
    min-height: 100%;
  }

  position: relative;

  .title {
    font-size: $content-title-size;
    width: $content-width;

    @include mq() {
      width: $content-tablet-width;
    }

    @include mq(sm) {
      width: 100%;
    }

    border-bottom: solid 1px $line-gray;

    p {
      margin: 0px;
      font-weight: bold;
      color: $title-gray;
    }
  }

  .content {
    margin-top: 10px;

    h1,
    h2,
    h3,
    p,
    span,
    li {
      color: $text-color;
    }

    img {
      width: 80%;
    }
  }
}

#footer {
  margin-top: 30px;
  padding-bottom: 30px;
  position: absolute;
  font-size: 10px;
  width: 100%;
  color: $title-gray;
  text-align: center;
  margin-bottom: 15px;
  opacity: 0.5;

  .copy {
    font-size: 14px;

    @include mq(sm) {
      font-size: 9px;
    }
  }

  a {
    color: $title-gray;
    text-decoration: underline;
  }
}

.top-margin {
  height: calc(#{$header-height} + #{$header-margin});
  width: 100%;

  @include mq(sm) {
    height: 60px;
    width: 100%;
  }
}

.room-tile {
  text-decoration: none;

  img {
    width: 100%;
    max-width: 380px;
    margin: 5px 0;
    border-radius: 12px;
    transition: 0.3s;
  }

  img:hover {
    opacity: 0.5;
  }
}

.room-tile-button {
  position: absolute;
  outline-style: none;
  font-weight: bold;
  background-color: #FF3464;
  border-radius: 32px;
  cursor: pointer;
  border: 0;
  padding: 18px;
  color: white;
  min-width: 150px;
  white-space: nowrap;
  font-size: 14pt;
  width: 8em;
}

.page-wrapper {
  transition: all 1.0s;
}

.opacity-zero {
  opacity: 0;
}

.opacity-one {
  opacity: 1;
}

.content-offset {
  @include content_offset();
}

.gaibu:after {
  content: "\f35d";
  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  margin: 0 5px;
  font-size: 0.9rem;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}

.gaibus {
  p:last-child:after {
    content: "\f35d";
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    margin: 0 5px;
    font-size: 0.9rem;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
  }
}

