@charset "utf-8";

$breakpoints: (
    'sm': 'screen and (max-width: 640px)',
    'tb': 'screen and (max-width: 768px)',
    'pc': 'screen and (min-width: 769px)',
    ) !default;

@mixin mq($breakpoint: tb) {
    @media #{map-get($breakpoints, $breakpoint)} {
        @content;
    }
}

$default-font-size: 15px;

$content-width: 1024px;

$content-tablet-width: 760px;

$content-mobile-width: 480px;

$side-width: 343px;

$footer-height: 50px;

$option-icon-size: 20px;

$subject-line-height: 30px;

$header-obj-height: 36px;

$content-title-size: 20px;

$content-subtitle-size: 16px;

$content-size: 14px;

$header-height: 60px;

$header-margin: 20px;

$default-text-color: #4f6269;

$base-green: #41bb19;

$bg-gray: #ebf0f0;

$bg-base: #f5f5f5;

$black: #333;

$white: #ffffff;

$green: #41bb19;

$white-green: #f0faf0;

$gray: #a0a0a0;

$title-gray: #666666;

$base-blue: #3399cc;

$light-gray: #a0a0d0;

$line-gray: #cccccc;

$text-color: #3d3d3d;

$content-title-color: #a9aeae;

$link-color: #008cdd;

$link-hover-color: #0069a6;

$background-color: #fafcff;

$hover-color: #e3edfc;



@mixin title() {
    font-size: 18px;
    font-weight: bold;
    color: $title-gray;
}

@mixin content_offset() {
    margin-left: 30px;
    margin-top: 10px;
}